import React from 'react'
import PropTypes from 'prop-types'

const Doms = function(props) {
  if (!props.suggestions_domains) {
    return null
  }
  let ListNames = []
  let Lists = []
  for (let name in props.suggestions_domains) {
    let list = props.suggestions_domains[name]
    if (list.length) {
      ListNames.push(name)
      Lists.push(
        list.map((phraseString, i) => (
          <div
            key={phraseString + i}
            className={'domname status' + (props.suggestions_availability[phraseString.replace(/ /g, '')] || 0)}
          >
            <span>
              {phraseString.split(' ').map((str, i) => (
                <span key={str + i} className="word">
                  {str}
                </span>
              ))}
            </span>
            &nbsp;&nbsp;
            <span className="domname-v">v</span>
          </div>
        )),
      )
    }
  }
  return (
    <div className="doms">
      {Lists.map((List, li) => (
        <div
          key={li}
          className="doms_group"
        >
          <h5>{ListNames[li] || 'suggestions'}</h5>
          <div>{List}</div>
        </div>
      ))}
    </div>
  )
}

Doms.propTypes = {
  suggestions_domains: PropTypes.object,
  suggestions_availability: PropTypes.object,
}

export default Doms
